const Evento = (props) => {

    const {name, url, description} = props.info;

    let des = description.text;
    let tit = name.text;

    if(tit.length > 100 ){
        tit = tit.substr(0, 100) + '...';
    }
    if(des){
        des = des.substr(0, 250) + '...';
    }

    return ( 
        <a href={url} target="_blank" className="list-group-item active text-light mb-1"><h3 className="mb-3">{tit}</h3>
        <p className="mb-1">{des}</p></a>
     );
}
 
export default Evento;